<template>
  <div id="bodyBox">
    <el-form :model="ruleForm"
             label-width="100px"
             label-position="left"
             class="demo-ruleForm">
      <el-form-item label="姓名"
                    prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="性别"
                    prop="sex">
        <el-radio-group v-model="ruleForm.sex">
          <el-radio label="男"></el-radio>
          <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="年龄"
                    prop="age">
        <el-input v-model.number="ruleForm.age"></el-input>
      </el-form-item>
      <el-form-item label="工作部门"
                    prop="department">
        <el-select v-model="ruleForm.department"
                   placeholder="请选择部门">
          <el-option label="前端开发"
                     value="前端开发"></el-option>
          <el-option label="测试工程师"
                     value="测试工程师"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="居住地"
                    prop="address">
        <el-input type="textarea"
                  v-model="ruleForm.address"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm()">立即创建</el-button>
        <el-button @click="resetForm()">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import {postEntry} from "@/api/api";
export default {
  data() {
    return {
      ruleForm: {
        username: "",
        department: "",
        sex: "",
        age: "",
        address: ""
      }
    };
  },
  methods: {
    submitForm() {
      console.log(this.ruleForm)
      if (
        this.ruleForm.username === "" ||
        this.ruleForm.department === "" ||
        this.ruleForm.sex === "" ||
        this.ruleForm.age === "" ||
        this.ruleForm.address === ""
      ) {
        this.$message({
          showClose: true,
          message: "请检查你的输入框,不能有空哦",
          type: "warning"
        });
      } else {
        alert("提交");
      }
    },
    resetForm() {
      for (const key in this.ruleForm) {
        this.ruleForm[key] = ''
      }
    }
  }
};
</script>
<style scoped>
#bodyBox {
  padding: 80px;
  width: 500px;
  text-align: left;
}
</style>